<template>
  <div class="register">
    <div class="container">
      <div class="row">
        <div class="col-xs-12 col-sm-12 col-md-7 register__box">
          <h1>ng k AI Call Center</h1>
          <h3>Tri nghim tng i t ng</h3>
          <b-form @submit="onSubmit" @reset="onReset" v-if="show">
            <div class="error" v-if="showErrors">
              <ul v-if="showErrors">
                <li v-for="(item, i) in errs" :key="i">{{item}}</li>
              </ul>
            </div>
            <div class="success" v-else-if="showSuccess">
              <p class="text-success">Cm n bn  ng k s dng AI Call Center, B phn chm sc khch hng ca chng ti s lin h vi qu khch trong thi gian sm nht.</p>
            </div>
            <b-form-group
              id="tenTongDai"
              label="Tn tng i:"
              label-for="name_tongdai"
            >
              <b-form-input
                id="name_tongdai"
                type="text"
                v-model="form.name_tongdai"
                required
                placeholder="Tn tng i"
              ></b-form-input>
            </b-form-group>

            <b-form-group
              id="tenCongTy"
              label="Tn cng ty:"
              label-for="name_congty"
            >
              <b-form-input
                id="name_congty"
                type="text"
                v-model="form.name_congty"
                required
                placeholder="Tn cng ty"
              ></b-form-input>
            </b-form-group>
            <b-form-group id="fullName" label="Tn ca bn:" label-for="fullName">
              <b-form-input
                id="fullName"
                type="text"
                v-model="form.name"
                required
                placeholder="Tn ca bn"
              ></b-form-input>
            </b-form-group>
            <b-form-group
              id="email"
              label="a ch Email:"
              label-for="email"
            >
              <b-form-input
                id="emailAddress"
                type="email"
                v-model="form.email"
                required
                placeholder="a ch mail"
              ></b-form-input>
            </b-form-group>
            <b-form-group
              id="sdt"
              label="S in thoi:"
              label-for="phone"
            >
              <b-form-input
                id="phone"
                type="text"
                v-model="form.phone"
                required
                placeholder="S in thoi"
              ></b-form-input>
            </b-form-group>
           
            <b-button type="submit" class="btn_register">ng k</b-button>
          </b-form>
        </div>

        <div class="col-xs-12 col-sm-12 col-md-5">
          <img src="../../assets/register/robo.png" alt="" class="img-fluid"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: "Register",
  data() {
    return {
      form: {
        email: "",
        name: "",
        phone: "",
        name_congty: "",
        name_tongdai: "",
        user_id: 0
      },
      show: true,
      showErrors: false,
      showSuccess: false,
      errs: []
    };
  },
  methods: {
    onSubmit(evt) {
      let me = this;
      evt.preventDefault();
      axios.post('ajaxfile.php', {
        request: 2,
        tongdai: this.form.name_tongdai,
        congty: this.form.name_congty,
        name: this.form.name,
        email: this.form.email,
        phone: this.form.phone
      })
      .then(function (response) {
        if(response.data.hasOwnProperty('err')) {
          me.showSuccess = false;
          me.showErrors = true;
          me.errs = [];
          me.errs.push(...response.data.err);
        } else {
          me.showErrors = false;
          me.showSuccess = true;
          let formGroups = me.$el.querySelectorAll('.form-group');
          for (let i of formGroups) {
            i.style.display = "none";
          }
          me.$el.querySelector('.btn_register').style.display = "none";  
        }
      })
    },
    onReset(evt) {
      evt.preventDefault();
      /* Reset our form values */
      this.form.email = "";
      this.form.name = "";
      this.form.name_congty = "";
      this.form.name_tongdai = "";
      this.form.phone = "";
      this.form.checked = [];
      /* Trick to reset/clear native browser form validation state */
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      });
    }
  },
  mounted(){

  }
};
</script>

<style scoped lang="scss">
@import './register.scss';
</style>